.social-list {
	@include listreset;

	display: flex;
	flex-wrap: wrap;
	font-size: 24px;

	&__item {
		display: inline-flex;
		margin-bottom: 10px;

		&:not(:last-child) {
			margin-right: 25px;
		}
	}

	&__link {
		display: inline-flex;
		color: $gray-500;
		text-decoration: none;
		transition: color 0.3s;

		&:hover {
			color: $purple;
		}
	}
}