.btn {
	text-transform: uppercase;

	&:not(.btn-sm):not(.btn-lg) {
		letter-spacing: 2px;
		font-weight: 500;

		@include media-breakpoint-down(lg) {
			font-size: 16px;
			padding: 0.6rem 0.75rem;
		}

		@include media-breakpoint-down(sm) {
			font-size: 14px;
			padding: 0.5rem 0.65rem;
		}

		&.btn-rounded {
			font-size: 18px;
			letter-spacing: 0.36px;
			border-radius: 25px;
			padding: 9px 20px;
			font-weight: 400;
			text-transform: inherit;
			border-width: 1px;

			@include media-breakpoint-up(md) {
				font-size: 20px;
			}
		}
	}

	&-lg {
		letter-spacing: 5.19px;
		min-width: 200px;

		@include media-breakpoint-up(sm) {
			min-width: 315px;
		}

		@include media-breakpoint-down(lg) {
			font-size: 20px;
		}

		@include media-breakpoint-down(md) {
			font-size: 18px;
			padding: 0.85rem 1.3rem;
		}
	}

	&-orange {
		color: $white;

		&:hover,
		&:focus {
			color: $white;
		}

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px $white;
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px $white;
			}
		}
	}

	&-purple {
		color: $white;

		&:hover,
		&:focus {
			color: $white;
		}

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px $secondary;
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px $secondary;
			}
		}

		&.with-dashed-border {
			border-color: $white;
		}
	}

	&-primary {
		color: $white;

		&:hover,
		&:focus {
			color: $white;
		}

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px darken($green, 25%);
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px darken($green, 25%);
			}
		}
	}

	&-light {
		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px $secondary;
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px $secondary;
			}
		}
	}

	&-outline-primary {
		background-color: $white;

		&:hover {
			color: $white;
		}

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px darken($green, 25%);
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px darken($green, 25%);
			}
		}
	}

	&-outline-light {
		background-color: $purple;

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px $secondary;
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px $secondary;
			}
		}
	}

	&-outline-purple {
		background-color: $white;

		&:hover {
			color: $white;
		}

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px $secondary;
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px $secondary;
			}
		}
	}

	&-light-gray {
		background-color: $light-gray;
		border-color: $light-gray;
		color: $purple;

		&:hover {
			color: $secondary;
		}

		&:not(.btn-sm):not(.btn-lg) {
			&:hover {
				box-shadow: 5px 5px $secondary;
			}
		}

		&.btn-lg {
			&:hover {
				box-shadow: 10px 10px $secondary;
			}
		}

		&.with-dashed-border {
			border-color: $purple;
		}
	}

	span {
		display: inline-block;
	}

	&.w-275 {
		@include media-breakpoint-up(md) {
			min-width: 275px;
		}
	}

	&.w-250 {
		@include media-breakpoint-up(md) {
			min-width: 250px;
		}
	}

	&.w-225 {
		@include media-breakpoint-up(md) {
			min-width: 225px;
		}
	}

	&.w-200 {
		@include media-breakpoint-up(md) {
			min-width: 200px;
		}
	}

	&.with-dashed-border {
		border-style: dashed;

		&:hover {
			border-color: transparent;
		}
	}
}

.buttons-list {
	@include listreset;

	@include min-w(450) {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		margin-bottom: 15px;
		display: flex;
    flex-direction: column;

		@include min-w(450) {
			display: block;
		}

		@include media-breakpoint-up(md) {
			margin-bottom: 25px;
		}

		&:not(:last-child) {
			@include min-w(450) {
				margin-right: 20px;
			}

			@include media-breakpoint-up(md) {
				margin-right: 40px;
			}
		}
	}
}