.review-card {
	border: 1px solid #979797;
	border-radius: 8px;
	margin-bottom: 30px;

	@include media-breakpoint-up(sm) {
		margin-bottom: 50px;
	}

	@include media-breakpoint-up(lg) {
		margin-bottom: 95px;
	}

	&__img-block {
		@include responsive-ratio(300, 200, true);

		position: relative;
		margin-top: -1px;
		margin-left: -1px;
		margin-right: -1px;
		border-radius: 8px;
		overflow: hidden;
	}

	&__img {
		@include absoluteCover;

		object-fit: cover;
	}

	&__body {
		position: relative;
		padding: 45px 20px 35px;

		&:after {
			@include sizeElement(47px);

			content: '\e902';
			position: absolute;
			top: 0;
			left: 13px;
			transform: translate(0, -40%);
			z-index: 5;
			display: inline-block;
			font-family: icomoon !important;
			border-radius: 50%;
			border: 1px solid #e7e7e7;
			box-shadow: 2px 2px 4px -2px rgba($black, 0.14);
			color: $primary;
			background-color: $white;
			text-align: center;
			padding: 5px;
			font-size: 15px;
		}
	}

	&__text {
		color: #7c7c7c;
		margin-bottom: 25px;
	}

	&__name-title {
		margin-bottom: 3px;
		font-size: 16px;
	}
}

.process-card {
	margin-bottom: 40px;

	&__img-block {
		@include sizeElement(160px);
		@include flexCenter;

		@include media-breakpoint-up(lg) {
			@include sizeElement(210px);
		}

		position: relative;
		margin: 0 auto 30px;
		background-color: $body-bg;

		&:before {
			display: block;
			width: 100vw;
			height: 4px;
			border-top: 4px dashed rgba($purple, 0.5);
			position: absolute;
			top: 50%;
			left: 100%;
			transform: translate(0, -50%);
		}

		&:after {
			display: block;
			height: 20px;
			background-color: $body-bg;
			width: 100vw;
			position: absolute;
			top: 50%;
			left: 100%;
			transform: translate(0, -50%);
		}
	}

	&__img {
		flex-shrink: 0;
		max-height: 100%;
	}

	&__body {
		text-align: center;

		@include media-breakpoint-up(xl) {
			padding: 0 25px;
		}
	}
}