@include title-sizes();

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	&.with-white-underline {
		position: relative;
		padding-bottom: 5px;

		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 0);
			display: block;
			height: 2px;
			width: 80%;
			max-width: 240px;
			background-color: $white;
		}
	}
}

h1, .h1 {
	letter-spacing: 0.24px;
	margin-bottom: 30px;
	line-height: 1;

	@include media-breakpoint-up(md) {
		margin-bottom: 40px;
	}

	small {
		text-transform: uppercase;
		font-size: 18px;
		letter-spacing: 6.49px;
		vertical-align: middle;
		line-height: 2;

		@include media-breakpoint-up(md) {
			font-size: 20px;
			line-height: 3;
		}

		@include min-w(lg) {
			font-size: 24px;
		}
	}
}

h2, .h2 {
	letter-spacing: 1px;
	margin-bottom: 40px;

	@include media-breakpoint-up(lg) {
		margin-bottom: 60px;
	}

	@include media-breakpoint-up(xl) {
		margin-bottom: 80px;
	}
}

h3, .h3 {
	margin-bottom: 40px;

	@include media-breakpoint-up(lg) {
		margin-bottom: 50px;
	}
}

h4, .h4 {
	font-weight: 500;
	margin-bottom: 30px;
}

h5, .h5 {
	font-weight: 500;
	margin-bottom: 15px;
	letter-spacing: 0.5px;
}

h6, .h6 {
	font-weight: 500;
	margin-bottom: 15px;
	letter-spacing: 0.1px;

	&.enlarged {
		@include media-breakpoint-up(xl) {
			font-size: 24px;
		}
	}
}

.text-small {
	font-size: $font-size-sm;
}

.text-large {
	font-size: 18px;

	@include media-breakpoint-up(md) {
		font-size: $font-size-lg;
	}
}

.list-with-quote-icon {
	@include listreset;

	font-size: 20px;
	font-weight: 500;
	letter-spacing: 0.43px;
	margin-bottom: 50px;

	@include media-breakpoint-up(lg) {
		font-size: 24px;
		margin-bottom: 75px;
	}

	li {
		position: relative;
		padding-left: 2.5em;

		&:not(:last-child) {
			margin-bottom: 1.333em;
		}

		&:before {
			content: '\e906';
			position: absolute;
			top: -0.083em;
			left: 0;
			font-family: icomoon!important;
			display: inline-block;
			color: $primary;
			font-size: 1.833em;
			line-height: 1;
		}
	}
}

.list-with-check {
	@include listreset;

	font-size: 18px;
	letter-spacing: 0.36px;

	@include media-breakpoint-up(sm) {
		font-size: 20px;
	}

	li {
		position: relative;
		padding-left: 30px;

		&:not(:last-child) {
			margin-bottom: 20px;
		}

		&:after {
			content: '\e901';
			font-family: icomoon!important;
			font-size: 1.1em;
			position: absolute;
			top: -1px;
			left: 0;
			color: $gray-900;
		}
	}

	+ .notice-text {
		padding: 25px 0 0 30px;
	}
}

.notice-text {
	font-size: 12px;
	font-style: italic;
}