.phone-with-side-info {
	@include media-breakpoint-up(md) {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__phone {
		margin-bottom: 50px;

		@include media-breakpoint-up(md) {
			flex: 0 0 auto;
			width: auto;
			margin-bottom: 0;
		}
	}

	&__info {
		@include media-breakpoint-up(md) {
			padding-left: 60px;
			max-width: 620px;
		}

		@include media-breakpoint-up(xl) {
			padding-left: 120px;
		}
	}

	&__info-title-holder {
		margin-bottom: 50px;
	}
}