.section {
	padding: 50px 0;

	@include media-breakpoint-up(md) {
		padding: 80px 0;
	}

	@include media-breakpoint-up(xl) {
		padding: 100px 0;
	}

	&-welcome {
		background: linear-gradient(to right, #d8d7ff, #ececfe 20%, #ececfe 80%, #d8d7ff 100%);

		&__text-content {
			margin-bottom: 50px;

			@include media-breakpoint-up(md) {
				margin-bottom: 0;
			}
		}

		&__text {
			font-size: 20px;
			letter-spacing: 1.08px;
			line-height: 1.1;
			max-width: 550px;
			margin-bottom: 30px;

			@include media-breakpoint-up(md) {
				font-size: 22px;
				margin-bottom: 45px;
			}

			@include media-breakpoint-up(lg) {
				font-size: 26px;
			}
		}

		&__small-title {
			font-weight: 500;
			font-size: 20px;
			letter-spacing: 0.1px;
			color: $gray-600;

			@include media-breakpoint-up(lg) {
				font-size: 23px;
			}
		}

		&__rating-block {
			margin-bottom: 50px;
		}
	}

	&-with-phones {
		background-image:
			url("../img/bg-shapes/1.svg"),
			url("../img/bg-shapes/2.svg"),
			url("../img/bg-shapes/3.svg")
		;
		background-repeat: no-repeat;
		background-position:
			left 5% top -25px,
			right 10% top 263px,
			right 50% top 700px
		;
		background-size: 200px, 100px, 90%;

		@include media-breakpoint-up(sm) {
			background-position:
				left 15% top -25px,
				right 15% top 263px,
				right 43% top 700px
			;
			background-size: 35%, 20%, 75%;
		}

		@include media-breakpoint-up(lg) {
			background-position:
				left 33% top -25px,
				right 23% top 263px,
				right 43% top 486px
			;
			background-size: 25%, 13%, 62%;
		}

		&__top-section {
			margin-bottom: 100px;

			@include media-breakpoint-up(md) {
				margin-bottom: 155px;
			}
		}

		&__title-holder {
			text-align: center;
			margin-bottom: 60px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 80px;
			}

			@include media-breakpoint-up(xl) {
				margin-bottom: 110px;
			}
		}

		&__phone-with-side-lists {
			margin-bottom: 60px;

			@include media-breakpoint-up(lg) {
				margin-bottom: 100px;
			}
		}
	}

	&-find-it-fast {
		background-color: $purple;
		color: $white;

		&__inner-content {
			@include media-breakpoint-up(xl) {
				padding: 0 50px;
			}
		}
	}

	&-our-simple-process {
		&__cards-block {
			margin-bottom: 30px;
			overflow: hidden;

			@include media-breakpoint-up(md) {
				margin-bottom: 55px;
			}

			[class^="col"] {
				&:nth-child(3n + 1) {
					.process-card {
						.process-card__img-block {
							&:before {
								@include media-breakpoint-up(md) {
									content: '';
								}
							}
						}
					}
				}

				&:nth-child(3n + 3),
				&:last-child {
					.process-card {
						.process-card__img-block {
							&:after {
								@include media-breakpoint-up(md) {
									content: '';
								}
							}
						}
					}
				}
			}
		}
	}

	&-our-service {
		background-image: url("../img/bg-shapes/4.svg");
		background-repeat: no-repeat;
		background-position: left 33% bottom;
		background-size: 1000px;

		@include media-breakpoint-up(md) {
			background-size: 80%;
		}

		@include media-breakpoint-up(xl) {
			background-size: 67%;
		}

		&__main-content {
			@include media-breakpoint-up(xl) {
				padding: 0 50px;
			}
		}

		&__banner {
			padding-top: 70px;

			@include media-breakpoint-up(lg) {
				padding-top: 15px;
			}
		}
	}

	&-guaranteed {
		background-color: $purple;
		color: $white;

		&__main-content {
			margin-bottom: 50px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 70px;
			}
		}

		&__img-label {
			max-width: 350px;
			margin: 0 auto 50px;

			@include media-breakpoint-up(md) {
				margin: 0;
				max-width: 100%;
			}
		}

		&__border-box {
			text-align: center;
			border: 4px solid $white;
			padding: 30px 15px 45px;
			max-width: 500px;
			margin-left: auto;
			overflow: hidden;

			@include media-breakpoint-up(sm) {
				padding: 30px 30px 45px;
			}
		}
	}

	&-qualifying-conditions {
		background-image: url("../img/bg-shapes/5.svg");
		background-repeat: no-repeat;
		background-position: right 36% top;
		background-size: 330px;

		@include media-breakpoint-up(sm) {
			background-size: 500px;
		}

		@include media-breakpoint-up(md) {
			background-size: 80%;
		}

		@include media-breakpoint-up(xl) {
			background-size: 67%;
		}

		&__main-content {
			@include media-breakpoint-up(xl) {
				padding: 0 50px;
			}
		}

		&__btn-block {
			text-align: center;
			padding-top: 50px;

			@include media-breakpoint-up(lg) {
				padding-top: 80px;
			}
		}
	}

	&-contact-us {
		background-image: url("../img/background-striped.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;

		&__main-content {
			margin-bottom: 40px;

			@include media-breakpoint-up(md) {
				margin-bottom: 60px;
			}

			@include media-breakpoint-up(lg) {
				margin-bottom: 95px;
			}
		}

		&__title-block {
			margin-bottom: 40px;
			text-align: center;

			@include media-breakpoint-up(md) {
				margin-bottom: 0;
				text-align: left;
			}

			@include media-breakpoint-up(xl) {
				padding-left: 50px;
			}
		}

		&__fields-set {
			@include media-breakpoint-up(md) {
				max-width: 450px;
			}
		}
	}

	&-faq {
		background-image:
			url("../img/bg-shapes/6.svg"),
			url("../img/bg-shapes/7.svg"),
			url("../img/bg-shapes/8.svg")
		;
		background-repeat: no-repeat;
		background-position:
			left -25% top 10px,
			right -2% top 380px,
			center bottom
		;
		background-size: 250px, 150px, 92%;

		@include media-breakpoint-up(sm) {
			background-size: 35%, 18%, 92%;
		}

		@include media-breakpoint-up(lg) {
			background-position:
				left 19% top 10px,
				right 10% top 380px,
				center bottom
			;
		}

		&__main-content {
			max-width: 850px;
			margin: 0 auto;
		}
	}

	&-reviews {
		&__grid-content {
			@include media-breakpoint-up(xl) {
				padding: 0 90px;
			}

			[class^="col"] {
				&:nth-last-child(-n+3) {
					.review-card {
						@include media-breakpoint-up(md) {
							margin-bottom: 0;
						}
					}
				}

				&:nth-last-child(-n+2) {
					.review-card {
						@include media-breakpoint-up(sm) {
							margin-bottom: 0;
						}
					}
				}
			}
		}

		&__row {
			@include media-breakpoint-up(xl) {
				margin-left: -35px;
				margin-right: -35px;
			}

			[class="col"],
			[class^="col-"] {
				@include media-breakpoint-up(xl) {
					padding-left: 35px;
					padding-right: 35px;
				}
			}
		}
	}
}