.rating {
	@include listreset;

	position: relative;
	color: #d2d4da;
	display: inline-flex;
	align-items: center;
	font-size: 20px;
	letter-spacing: 9px;

	li {
		display: inline-flex;
		line-height: 1;

		&:last-child {
			letter-spacing: 0;
		}
	}

	&:after {
		content: "\e900 \e900 \e900 \e900 \e900";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
		display: inline-flex;
		font-family: icomoon!important;
		line-height: 1;
		color: $yellow;
		overflow: hidden;
	}

	&[data-rating="5"] {
		&:after {
			width: 100%;
		}
	}

	&[data-rating="4.5"] {
		&:after {
			width: 92%;
		}
	}

	&[data-rating="4"] {
		&:after {
			width: 80%;
		}
	}

	&[data-rating="3.5"] {
		&:after {
			width: 71%;
		}
	}

	&[data-rating="3"] {
		&:after {
			width: 60%;
		}
	}

	&[data-rating="2.5"] {
		&:after {
			width: 50%;
		}
	}

	&[data-rating="2"] {
		&:after {
			width: 40%;
		}
	}

	&[data-rating="1.5"] {
		&:after {
			width: 28%;
		}
	}

	&[data-rating="1"] {
		&:after {
			width: 20%;
		}
	}

	&[data-rating="0.5"] {
		&:after {
			width: 7%;
		}
	}

	&[data-rating="0"] {
		&:after {
			width: 0%;
		}
	}
}

.rating-with-title {
	display: flex;
	align-items: center;
	flex-wrap: wrap;

	&__title {
		margin-bottom: 0;
	}

	&__rating {
		padding-left: 15px;
	}
}