.quote-bg-with-title {
	position: relative;
	display: inline-block;
	max-width: 270px;
	width: 100%;

	@include media-breakpoint-up(lg) {
		max-width: 350px;
	}

	&__title {
		position: absolute;
		top: 30%;
		left: 50%;
		transform: translate(-50%, 0);
		color: $white;
		width: 100%;
		text-align: center;
		margin-bottom: 0;
	}
}