.banner-with-price {
	box-shadow: 1px 1px 25px rgba($black, 0.25);
	border-radius: 33px;
	border: 1px solid #979797;
	background-color: $green-faded;
	padding: 35px 20px;
	max-width: 500px;

	@include media-breakpoint-up(sm) {
		padding: 35px 60px 60px;
	}

	&__button-block {
		text-align: center;
		padding-top: 40px;

		@include media-breakpoint-up(sm) {
			padding-top: 60px;
		}
	}
}