.footer {
	padding: 50px 0 20px;
	background-color: $gray-200;

	@include media-breakpoint-up(sm) {
		padding: 50px 0;
	}

	&__content-wrap {
		@include media-breakpoint-up(sm) {
			border-top: 1px solid #9c9c9d;
			border-bottom: 1px solid #9c9c9d;
			padding: 50px 0;
		}

		@include media-breakpoint-up(lg) {
			padding: 120px 0 75px;
		}
	}

	&__copyright {
		padding-top: 30px;
		text-align: center;
		font-size: 16px;
		letter-spacing: 0.09px;
		color: $gray-600;

		@include media-breakpoint-up(md) {
			font-size: 18px;
		}

		@include media-breakpoint-up(lg) {
			padding-top: 50px;
		}
	}
}

.footer-menu-title {
	color: $gray-900;
}

.footer-menu {
	@include listreset;

	font-size: 16px;
	letter-spacing: 0.1px;
	margin-bottom: 40px;

	@include media-breakpoint-up(md) {
		font-size: 21px;
	}

	@include media-breakpoint-up(xl) {
		font-size: 23px;
	}

	&__item {
		&:not(:last-child) {
			margin-bottom: 15px;
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		color: $gray-600;
		text-decoration: none;

		&:after {
			content: "";
			position: absolute;
			bottom: -1px;
			left: 0;
			width: 100%;
			height: 2px;
			background: linear-gradient(254.54deg, $purple 0%, rgba($purple, 0) 100%);
			transform: scaleX(0);
			transform-origin: right center;
			transition: transform 0.5s cubic-bezier(0.694, 0, 0.335, 1);
		}

		&:hover {
			color: $gray-600;

			&:after {
				transform: scaleX(1);
				transform-origin: left center;
			}
		}
	}
}