.phone-with-side-lists {
	@include media-breakpoint-up(lg) {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__list {
		@include media-breakpoint-up(lg) {
			max-width: 350px;
			width: 100%;
		}
	}

	&__phone {
		flex: 0 0 auto;
    width: auto;
		padding: 50px 0;

		@include media-breakpoint-up(lg) {
			padding: 0 50px;
		}
	}
}