.accordion {
	&-item {
		position: relative;
		border-radius: $accordion-border-radius;
		border-top: $accordion-border-width solid $accordion-border-color !important;

		&:not(:last-child) {
			margin-bottom: 20px;

			@include media-breakpoint-up(md) {
				margin-bottom: 40px;
			}
		}

		&:hover {
			.accordion-button {
				color: $purple;
			}
		}
	}

	&-button {
		position: static;
		font-size: 18px;
		font-weight: 500;
		letter-spacing: 0.43px;
		border-radius: 0;
		padding-right: 60px;

		@include media-breakpoint-up(sm) {
			font-size: 20px;
		}

		@include media-breakpoint-up(md) {
			font-size: 24px;
		}

		@include media-breakpoint-up(lg) {
			padding-right: 135px;
		}

		@include media-breakpoint-down(lg) {
			padding-top: 20px;
			padding-bottom: 20px;
		}

		@include media-breakpoint-down(sm) {
			padding-left: 15px;
		}

		&:before {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 1;
		}

		&:after {
			position: absolute;
			top: 50%;
			right: 25px;
			transform: translate(0, -50%);
			background-position: center;
			transition: opacity 0.3s;

			@include media-breakpoint-up(lg) {
				right: 70px;
			}
		}

		&:not(.collapsed) {
			box-shadow: none;

			&:after {
				opacity: 0;
				transform: translate(0, -50%);
			}
		}
	}

	&-body {
		padding-right: 28px;
		padding-top: 0;
		letter-spacing: 0.36px;
		line-height: 1.2;

		@include media-breakpoint-down(lg) {
			padding-bottom: 15px;
		}

		@include media-breakpoint-down(sm) {
			padding-left: 15px;
			padding-right: 15px;
		}

		@include media-breakpoint-up(lg) {
			padding-bottom: 16px;
		}
	}

	&-collapse {
		@include media-breakpoint-up(lg) {
			transform: translate(0px, -20px);
		}
	}
}