.hero-chat-bubble-list {
	@include listreset;

	max-width: 400px;
	margin: 0 auto;
	text-align: right;

	@include media-breakpoint-up(sm) {
		max-width: 350px;
	}

	@include media-breakpoint-up(md) {
		max-width: 100%;
	}

	&__item {
		&:nth-child(2n) {
			@include media-breakpoint-down(sm) {
				text-align: left;
			}

			.hero-chat-bubble-list__item-inner {
				@include media-breakpoint-up(sm) {
					transform: translate(-80px, 0);
				}

				@include media-breakpoint-up(xl) {
					transform: translate(-120px, 0);
				}
			}

			.hero-chat-bubble-list__photo-block {
				left: auto;
				right: 13%;
			}
		}
	}

	&__item-inner {
		display: inline-block;
		position: relative;
		max-width: 300px;

		@include media-breakpoint-up(sm) {
			max-width: 100%;
		}
	}

	&__photo-block {
		@include sizeElement(60px);

		position: absolute;
		top: 48%;
    left: 13%;
		transform: translate(0, -50%);
		z-index: 5;
		border-radius: 50%;
		overflow: hidden;

		@include media-breakpoint-up(lg) {
			@include sizeElement(75px);
		}

		@include media-breakpoint-up(xl) {
			@include sizeElement(100px);
		}
	}

	&__photo-img {
		@include absoluteCover;

		object-fit: cover;
	}
}