@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?x57dtn');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?x57dtn#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?x57dtn') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?x57dtn') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?x57dtn##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-quote {
  &:before {
    content: $icon-quote; 
  }
}
.icon-star {
  &:before {
    content: $icon-star; 
  }
}
.icon-round-check {
  &:before {
    content: $icon-round-check; 
  }
}
.icon-brackets {
  &:before {
    content: $icon-brackets; 
  }
}
.icon-facebook {
  &:before {
    content: $icon-facebook; 
  }
}
.icon-google {
  &:before {
    content: $icon-google; 
  }
}
.icon-twitter {
  &:before {
    content: $icon-twitter; 
  }
}

